  div.header{
      position: sticky;
  }
  .header {
    cursor: pointer;
    display: flex;
    top: 0px;
    margin: 0px;
    /* display: flexbox; */
    position: sticky;
    background-color: whitesmoke;
    height: 63px;
    /* justify-content: center; */
    vertical-align: center;
    /* padding: 10px; */
    flex-direction: row;
    z-index: 10;
  }
  button:hover {
    /* background-color: #04a46d; */
    cursor: pointer;
  }
  .top-left button {
    margin: 0px;
    display: inline-block;
  }
  .header-left {
    float: left;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
  
    /* font-size: 2em; */
  }
  .header-right {
    float: right;
    width: 50%;
    text-align: right;
    margin-top: -5px;
    /* margin-right: 20px; */
  }
  .header button {
    font-size: 1em;
    font-weight: bold;
    display: inline-block;
    margin: none;
  }
  button {
    border: none;
    background: none;
    outline: none;
  }
  .other-btns:hover {
    background-color: aquamarine;
  }
  
  .top-right {
    /* height: 60%; */
    /* float: right; */
