  /* -----------------------------------------------------------------cart------------------------------- */
  .page-contents {
      width: 100%;
      margin-top: 0px;  
      margin-right: 20px;
      /* z-index: -1; */
      background-color: moccasin;
  } 
  .cart {
    box-sizing: border-box;
    /* display: none;   */
    background-color: moccasin;
    position: fixed;
    top: 17%;
    right: 0px;
    height: calc(100%);
    width: 20%;
    z-index: 2;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  }
  
  .display-cart {
    display: block;
  }
  
  .bar{
    box-shadow: inset 0 0 35px coral;
    border: solid 2px;
    position: absolute;
    margin-top: 0;
    width: 100%;
    height: 40px;
    padding: 5px;
    justify-content: space-around;
    background-color: #1944d3;
  }
  
  .display-area{
    display: flex;
  }
  .display-left{
    margin-top: 50px;
    border:solid ;
  border-width: 2px 0px 2px 2px;
  /* box-shadow: inset 0px 0px 5px 5px rgba(123, 6, 28, 0.2) */
  
  }
  .display-right{
    margin-top: 50px;
    border:solid 2px;
    /* box-shadow: inset 0px 0px 5px 5px rgba(123, 6, 28, 0.2) */
  
  
  }
  .display-area-text{
    display: inline;
    float: right;
    width: 150px;
    /* width: 50%; */
    height: 300px;
    border: solid;
    border-width: 2px 0 2px 2px;
    /* box-shadow: inset 0px 0px 10px 10px rgba(123, 6, 28, 0.2) */
  
  }
  .display-area-value{
    display: inline;
    float: right;
    width: 150px;
    /* width: 50%; */
    height: 300px;
    border: solid 2px;
    /* box-shadow: inset 0px 0px 10px 10px rgba(123, 6, 28, 0.2) */
  
  }
  
  .cart h2{
    
    margin-top: 10px;
  }
  
  .close-button {
    position: absolute;
    top: 25px;
    right: 10px;
    background-color: #ccc;
    border: none;
    cursor: pointer;
  }
  .total-area{
    vertical-align: bottom;
    /* margin-bottom: 10px; */
    padding-top: 20px;
    border: solid 2px;
    width: 100%;
    padding: 20px;
  
  
  }
  
  @media all and (max-width:1100px)
  {
    div.cart
    {
      display: none !important;
    }
